.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 75%;
    height: 75%;
    transform: translate(-50%, -50%);
    background-color: var(--kinda-white);
    padding: 18px;
    z-index: 1000;
    border-radius: 15px;
    /* transition: all 0.5s ease-in-out; */
}

.modalContainer {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
}

.photoContainer {
    width: 50%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1em;
    /* background: rgb(24, 24, 24); */
}

.photoContainer>img {
    max-width: 100%;
    max-height: 100%;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
    border-radius: 15px;
}

.modalDescription {
    margin: 0 auto;
    width: 40%;
    margin-top: 40px;
}

.modalIcons {
    display: block;
    float: right;
    z-index: 3;
    position: absolute;
    right: 5px;
    top: 5px;
}

.modal-close-icon {
    cursor: pointer;
    font-size: var(--icon-size);
    margin-right: 10px;
    margin-top: 5px;
    padding-right: 12px;
    padding-left: 12px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 10%;
    transition: all 0.4s ease;
}

.modal-message-icon {
    cursor: pointer;
    font-size: var(--icon-size);
    margin-right: 10px;
    margin-top: 5px;
    padding: 10px;
    border-radius: 10%;
    transition: all 0.4s ease;
}

.modal-close-icon:hover,
.modal-message-icon:hover {
    /* transform: scale(1.2);  */
    background-color: rgb(207, 207, 207);
}

.modal-title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    overflow-x: scroll;
    gap: 0.3em;
}

.modal-pet-name {
    font-family: "Paytone One";
    font-size: 3em;
    font-weight: bold;
    text-transform: uppercase;
    text-shadow: 2px 2px #dadada;
    letter-spacing: 1px;
}

.modal-pet-species {
    font-family: "Paytone One";
    font-size: 25px;
    margin-top: 10px;
    font-weight: 600;
    text-transform: uppercase;
    text-shadow: 2px 2px #e6e6e6;
    letter-spacing: 1px;
}

.modal-description-header {
    text-align: center;
    justify-content: center;
    font-size: 28px;
    font-family: sans-serif;
    font-weight: bold;
    position: relative;
    margin: 1em auto;
}

.modal-description-header:before {
    content: "";
    display: block;
    background: #000;
    width: 38%;
    height: 4px;
    position: absolute;
    left: 0;
    top: 45%;
    z-index: -2;
    border-radius: 100px;
}

.modal-description-header:after {
    content: "";
    display: block;
    background: #000;
    width: 38%;
    height: 4px;
    position: absolute;
    right: 0;
    top: 45%;
    z-index: -2;
    border-radius: 100px;
}

.modal-description {
    font-family: "Paytone One";
    background: var(--kinda-white);
    text-shadow: 2px 2px #e6e6e6;
    font-size: 1.1em;
    padding: 0.5em;
    font-weight: bold;
    width: 100%;
    /* display: flex; */
}

.modal-pet-description {
    margin-top: 20px;
    font-family: "Paytone One";
    font-size: 1.2em;
    text-align: justify;
    text-justify: inter-word;
    overflow-y: scroll;
    line-height: 1.6em;
    text-indent: 25px;
}

.modal-contact {
    width: 100%;
}

.modal-contact>div {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.modal-description>div {
    padding: 0.5em;
    padding-left: 1em;
    width: 5em;
}

.modal-contact>div>span {
    width: 50%;
    font-weight: normal;
    padding-left: 2em;
}