@import url("https://fonts.googleapis.com/css2?family=Anton&display=swap");
:root {
    --login-page-height: calc(100vh);
    --page-height-with-navbar: calc(100vh - 3em);
    --navyblue: #002040;
    --brown: #7b4935;
    --light-brown: #ebc7b2;
    --kinda-white: #f5f5f5;
    --kinda-grey: #c1c1c1;
    --dark-grey: #c4c2c2;
    --hover-background: rgb(207, 207, 207);
    --icon-size: 18px;
}

* {
    /* Scrollbars */
    /* -ms-overflow-style: none; */
    scrollbar-width: 10px;
    /* overflow-y: scroll; */
    transition: all 0.3s ease-in-out;
    /* element styling */
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*::-webkit-scrollbar {
    display: none;
}

body {
    height: 100%;
    width: 100%;
}

.add-outer {
    width: 100vw;
    height: var(--page-height-with-navbar);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--light-brown);
}